import React from "react";
import { Pie } from "@reactchartjs/react-chart.js";
import theme from "../theme";

export default function PieChart({ downloads }) {
  const getLabels = () => {
    let lables = ["Android", "iOS"];
    const sum = downloads.reduce((a, b) => a + b);
    lables.forEach((label, i) => {
      lables[i] = label + " " + ((downloads[i] / sum) * 100).toFixed(2) + "%";
    });

    return lables;
  };
  const data = {
    labels: getLabels(),
    datasets: [
      {
        label: getLabels(),
        data: downloads,
        backgroundColor: [
          theme.palette.platforms.Android,
          theme.palette.platforms.iOS,
        ],
        borderWidth: 1,
      },
    ],
  };

  const options = {
    tooltips: {
      enabled: false,
    },
    legend: {
      display: true,
      reverse: true,
      onClick: function () {
        return;
      },
    },
  };

  return (
    <div>
      <Pie data={data} options={options} />
    </div>
  );
}
