import { useSelector } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import Avatar from "@material-ui/core/Avatar";
import theme from "../theme";
import DownloadsFormatter from "../util/DownloadsFormatter";
import { useDispatch } from "react-redux";
import { fetchAmplitudeData } from "../redux/reducer";

const useStyles = makeStyles({
  root: {
    maxWidth: 300,
    width: "100%",
    margin: "0 auto 1em",
    position: "relative",
    "& .MuiCardHeader-title": {
      fontSize: "1.7em",
      textIndent: "-1em",
      fontWeight: 500,
    },
    "& .MuiCardHeader-subheader": {
      position: "absolute",
      right: "1em",
      top: "50%",
      transform: "translateY(-50%)",
      color: theme.palette.success.main,
      transition: "all .3s",
    },
  },
  totalDownloadsHeader: {
    "& .MuiCardHeader-content": {
      marginTop: "-1em",
    },
  },
  updateDate: {
    width: "100%",
    position: "absolute",
    bottom: "1em",
    left: "50%",
    color: "#9e9e9e",
    transform: "translateX(-50%)",
    textIndent: "3em",
    fontSize: ".8em",
  },
});

export default function InfoCard({ platform, downloads }) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const increments = useSelector((state) => state.app.increments[platform]);
  const updateDate = useSelector((state) => state.app.lastUpdateDate);
  const yearFilter = useSelector((state) => state.app.filterByYear);
  const dataType = useSelector((state) => state.app.dataType);
  const subheader =
    increments !== downloads && increments > 0 && yearFilter === "total"
      ? `+${increments}`
      : false;

  return (
    <Card className={classes.root}>
      <CardHeader
        className={platform === "total" ? classes.totalDownloadsHeader : ""}
        avatar={
          <Avatar
            aria-label="recipe"
            style={{ backgroundColor: theme.palette.platforms[platform] }}
            onClick={() => platform === "total" && dispatch(fetchAmplitudeData(dataType))}
          >
            {theme.icons[platform === "total" ? dataType : platform]}
          </Avatar>
        }
        title={DownloadsFormatter(downloads)}
        subheader={subheader}
      />
      {platform === "total" && updateDate && (
        <div className={classes.updateDate}>{updateDate}</div>
      )}
    </Card>
  );
}
