import React from "react";
import { Bar } from "@reactchartjs/react-chart.js";
import theme from "../theme";

export default function GroupedBar({ months, downloadsByMonths }) {
  const data = {
    labels: months,
    datasets: [
      {
        label: "Android",
        data: downloadsByMonths.Android,
        backgroundColor: theme.palette.platforms.Android,
      },
      {
        label: "iOS",
        data: downloadsByMonths.iOS,
        backgroundColor: theme.palette.platforms.iOS,
      },
    ],
  };

  const options = {
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
          },
        },
      ],
    },
    legend: {
      display: false,
    },
  };

  return <Bar data={data} options={options} />;
}
