import React from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import { getDataByYear } from "../redux/reducer";
import { useSelector, useDispatch } from "react-redux";

const useStyles = makeStyles({
  root: {
    display: "flex",
    justifyContent: "center",
    margin: "1em auto 0",
    "& > button": {
      margin: "0 .45em",
    },
  },
});

export default function FilterButtons() {
  const classes = useStyles();
  const years = useSelector((state) => state.app.dataByYears);
  const yearFilter = useSelector((state) => state.app.filterByYear);
  const dispatch = useDispatch();
  const filterButtons = Object.keys(years)
    .reverse()
    .map((year, i) => {
      return (
        <Button
          variant={yearFilter === year ? "contained" : "outlined"}
          color="primary"
          key={i}
          onClick={() => dispatch(getDataByYear(year))}
        >
          {year}
        </Button>
      );
    });

  return (
    <div className={classes.root}>
      <Button
        variant={yearFilter === "total" ? "contained" : "outlined"}
        color="primary"
        onClick={() => dispatch(getDataByYear("total"))}
      >
        All
      </Button>
      {filterButtons}
    </div>
  );
}
