import React from "react";
import Alert from "@material-ui/lab/Alert";
import { makeStyles } from "@material-ui/core/styles";
import { useSelector } from "react-redux";
import { dataUpdateFailureMessage } from "../util/messages";

const useStyles = makeStyles({
  root: {
    justifyContent: "center",
    marginTop: "1em",
  },
});

export default function ServerAlert() {
  const classes = useStyles();
  const message = useSelector((state) => state.app.serverErrorMessage);

  return (
    <Alert className={classes.root} severity="error">
      {message || dataUpdateFailureMessage}
    </Alert>
  );
}
