import React from "react";
import { Line } from "@reactchartjs/react-chart.js";
import { purple } from "@material-ui/core/colors";

export default function LineChart({ months, downloadsByMonths }) {
  const totalDownloads = [];
  for (let i = 0; i < downloadsByMonths.Android.length; i++) {
    totalDownloads.push(
      downloadsByMonths.Android[i] + downloadsByMonths.iOS[i]
    );
  }

  const data = {
    labels: months,
    datasets: [
      {
        label: "",
        data: totalDownloads,
        backgroundColor: purple[500],
        fill: false,
        pointRadius: 6,
        pointHoverRadius: 10,
      },
    ],
  };

  const options = {
    legend: {
      display: false,
    },
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
          },
        },
      ],
    },
  };

  return <Line data={data} options={options} />;
}
