import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Grid from "@material-ui/core/Grid";
import FilterButtons from "../FilterButtons";
import ServerAlert from "../ServerAlert";
import theme from "../../theme"
import TrendingUpIcon from '@material-ui/icons/TrendingUp';
import { useSelector } from 'react-redux'

const useStyles = makeStyles({
  table: {
    marginTop: 20,
  },
  Android: {
    color: theme.palette.platforms.Android
  },
  iOS: {
    color: theme.palette.platforms.iOS
  },
  total: {
    color: theme.palette.platforms.total
  },
  diff: {
    color: theme.palette.primary.main
  },
  months: {
    minWidth: 75,
    paddingRight: 10
  },
  td: {
    padding: 6
  },
  success: {
    color: theme.palette.success.main
  },
  warning: {
    color: theme.palette.warning.main
  },
});

export default function ActivityScreen({ data, error }) {
  const classes = useStyles()
  const dataByYears = useSelector((state) => state.app.dataByYears)
  const year = useSelector((state) => state.app.filterByYear)

  const rows = [];
  const createRow = (month, Android, iOS, total, diff, color) => {
    return { month, Android, iOS, total, diff, color }
  }
  const getDiff = (monthIndex) => {
    const AndroidUsers = data.downloadsByMonths.Android[monthIndex]
    const iOSUsers = data.downloadsByMonths.iOS[monthIndex]
    let formerAndroidUsers = data.downloadsByMonths.Android[monthIndex - 1]
    let formerOSUsers = data.downloadsByMonths.iOS[monthIndex - 1]
    if (monthIndex === 0 && year !== "total" && year > 2019) {
      const prevYearAndroidUsers = dataByYears[year - 1].downloadsByMonths.Android
      const prevYeariOSUsers = dataByYears[year - 1].downloadsByMonths.iOS
      formerAndroidUsers = prevYearAndroidUsers[prevYearAndroidUsers.length - 1]
      formerOSUsers = prevYeariOSUsers[prevYeariOSUsers.length - 1]
    }
    return (AndroidUsers + iOSUsers) - (formerAndroidUsers + formerOSUsers) || '';
  }

  for (let i = 0; i < data.months.length; i++) {
    const month = data.months[i];
    const AndroidUsers = data.downloadsByMonths.Android[i]
    const iOSUsers = data.downloadsByMonths.iOS[i]
    const total = AndroidUsers + iOSUsers
    const diff = getDiff(i);
    rows.unshift(createRow(month, AndroidUsers, iOSUsers, total, diff, diff > 0 ? "success" : "warning"))
  }

  return (
    <>
      <Grid item xs={12}>
        {!error ? <FilterButtons /> : <ServerAlert />}
      </Grid>
      <TableContainer component={Paper} className={classes.table}>
        <Table size="small" aria-label="activity table">
          <TableHead>
            <TableRow>
              <TableCell></TableCell>
              <TableCell align="center" className={classes.Android}>{theme.icons.Android}</TableCell>
              <TableCell align="center" className={classes.iOS}>{theme.icons.iOS}</TableCell>
              <TableCell align="center" className={classes.total}>{theme.icons.active}</TableCell>
              <TableCell align="center" className={classes.diff}>{<TrendingUpIcon />}</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row) => (
              <TableRow key={row.month}>
                <TableCell component="th" scope="row" className={classes.months}>{row.month}</TableCell>
                <TableCell align="center" className={classes.td}>{row.Android}</TableCell>
                <TableCell align="center" className={classes.td}>{row.iOS}</TableCell>
                <TableCell align="center" className={classes.td}>{row.total}</TableCell>
                <TableCell align="center" className={`${classes.td} ${classes[row.color]}`}>
                  {row.color === "success" && "+"}{row.diff}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
}
