import React from 'react'
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import InfoCard from "../InfoCard";
import FilterButtons from "../FilterButtons";
import PieChart from "../PieChart";
import GroupedBar from "../GroupedBar";
import LineChart from "../LineChart";
import ServerAlert from "../ServerAlert";

const useStyles = makeStyles({
  block: {
    marginTop: "1.5em",
  },
  standout: {
    marginTop: "1.5em",
    marginBottom: "1em",
  },
});

export default function DownloadsScreen({ data, error }) {
  const classes = useStyles();
  return (
    <>
      <Grid item xs={12} className={classes.standout}>
        <InfoCard platform="total" downloads={data.total} />
      </Grid>

      <Grid item xs={12}>
        <InfoCard
          platform="Android"
          downloads={data.Android}
        />
      </Grid>

      <Grid item xs={12}>
        <InfoCard platform="iOS" downloads={data.iOS} />
      </Grid>

      <Grid item xs={12}>
        {!error ? <FilterButtons /> : <ServerAlert />}
      </Grid>

      <Grid item xs={12} className={classes.block}>
        <LineChart
          months={data.months}
          downloadsByMonths={data.downloadsByMonths}
        />
      </Grid>

      <Grid item xs={12} className={classes.block}>
        <GroupedBar
          months={data.months}
          downloadsByMonths={data.downloadsByMonths}
        />
      </Grid>

      <Grid item xs={12} className={classes.standout}>
        <PieChart downloads={[data.Android, data.iOS]} />
      </Grid>
    </>
  )
}
